import { request } from '../utils/request'


export function getPerson(id) {   //登录
    return request({
        method: 'get',
        url: `wb/user/detail?userId=` + id,
    })
}

export function getSms(query) {   //登录
    return request({
        method: 'get',
        url: `wb/user/sms`,
        params: query
    })
}

export function sendMessage(data) {   //留言
    return request({
        method: 'post',
        url: `wb/user/message`,
        data
    })
}

