<template>
    <div class="personal commpage">
        <el-card class="box-card">
            <div slot="header" class="clearfix">
                <span>基本信息</span>
            </div>
            <div class="person-card">
                <div class="card-left">
                    <div>井队名称</div>
                    <div>{{team?team:'-'}}</div>
                </div>
                <div class="card-right">
                    <div>用电性质</div>
                    <div>{{work_type?work_type:'-'}}</div>
                </div>
            </div>
        </el-card>
        <div class="person-tabs">
            <div class="tabs-head">
                <el-tabs v-model="activeIndex" @tab-click="tabClick">
                    <el-tab-pane label="停电通知" name="1">
                    </el-tab-pane>
                    <el-tab-pane label="送电通知" name="2">
                    </el-tab-pane>
                    <el-tab-pane label="异常警告" name="3">
                    </el-tab-pane>
                    <el-tab-pane label="待办事项" name="4">
                    </el-tab-pane>
                </el-tabs>

                <!-- <div v-for="(item,index) in tabs" @click="handTab(index+1)" :key="item.value"
                    :class="item.value===checkedTab?'is-active':''">{{item.text}}</div> -->

            </div>
            <div class="tabs-cont">
                <div class="list-Item" v-for="item in listData" :key="item.id">
                    <div class="list-left">{{formatDate(item.crtTime)}} , {{item.content}}</div>
                    <div class="list-right">
                        <div class="detail-btn" @click="toDetail(item)">查看</div>
                    </div>
                </div>
            </div>

        </div>
        <el-card class="box-card smsTop">
            <div slot="header" class="clearfix">
                <span>在线客服</span>
                <div class="person-info">
                    <div>客服经理：{{kfname}}</div>
                    <div>电话：<span>{{kfphone}}</span></div>
                </div>
            </div>
            <div class="person-card">
                <div class="person-textarea">
                    <el-input type="textarea" :autosize="{ minRows: 2, maxRows: 4}" placeholder="请输入内容"
                        v-model="content">
                    </el-input>
                    <div class="btn-box">
                        <div class="submit" @click="sendMessage">发 送</div>
                        <!-- <div class="cancle">取消</div> -->
                    </div>
                </div>
            </div>
        </el-card>
        <el-dialog title="提示" :visible.sync="dialogVisible" width="30%" :before-close="handleClose">
            <span>{{itemContent}}</span>
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible = false">取 消</el-button>
                <el-button type="primary" @click="dialogVisible = false">确 定</el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
    import {
        getSms,
        sendMessage
    } from '@/api/personal'
    import {
        mapState
    } from 'vuex'
    import {
        formatDate,
        formatTime
    } from '@/utils/index'
    export default {
        name: 'index',
        data() {
            return {
                formatDate,
                formatTime,
                activeIndex: '1', //默认激活第一项
                listData: [],
                textarea: '',
                work: {},
                content: '', //留言内容
                dialogVisible: false,
                itemContent: '',
            }
        },
        created() {
            this.getSms();
        },
        computed: {
            ...mapState({
                userId: state => state.user.userId,
                work_type: state => state.user.work_type,
                team: state => state.user.team,
                teamShortName: state => state.user.teamShortName,
                kfname: state => state.user.kfname,
                kfphone: state => state.user.kfphone,

            })
        },
        mounted() {},
        methods: {
            tabClick() {
                this.getSms()
            },
            getSms() {
                getSms({
                    userId: this.userId,
                    type: this.activeIndex
                }).then(res => {
                    this.listData = res.data;
                })
            },
            sendMessage() {
                let data = {
                    content: this.content,
                    userId: this.userId
                }
                sendMessage(data).then(res => {
                    this.$message({
                        message: '留言成功！',
                        type: 'success'
                    })
                })
            },
            toDetail(item) {

                this.itemContent = item.content
                this.dialogVisible = true
            },
            handleClose() {
                this.dialogVisible = false
            }


        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
    .commpage {
        width: 100%;
